import React from 'react';
const About = () => {
  return (
    <section id="about" className="py-20 ">
      <div className="container mx-auto text-center">
        <h2 className="text-4xl font-bold mb-6">About Me</h2>
        <p className="text-lg max-w-2xl mx-auto">
          I am a passionate designer and video editor with experience in creating stunning visuals and engaging videos. 
          I work with clients to bring their ideas to life and create memorable content.
        </p>
      </div>
    </section>
  );
};

export default About;
